@media screen and (min-width: 768px) {
  .checkbox-header {
    width: 40px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0);
    margin-right: 12px;
    display: inline-block;
    position: relative;
  }
}

.checkbox-header__input {
  display: none;
}

.checkbox-header__switch-slider {
  cursor: pointer;
  background-color: #fff;
  border: .6px solid #4b48db;
  border-radius: 20px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.checkbox-header__switch-slider:before {
  content: "";
  cursor: pointer;
  height: 16px;
  width: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.checkbox-header__input:checked + .checkbox-header__switch-slider {
  background-color: #4440f7;
}

.checkbox-header__input:checked + .checkbox-header__switch-slider:before {
  background-color: #fff;
  transform: translateX(20px);
}

