}

.panel {
  background-color: inherit;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 28px 30px;
  padding: 0 18px;
  display: flex;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .panel {
    justify-content: start;
    row-gap: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .panel {
    gap: 40px 30px;
  }
}

.accordion--dark {
  color: var(--third-color);
}

